"use client"

import { motion } from "framer-motion"
import { Vote, Car, Plane, CreditCard, FileText, Hash, Building2, Phone, ShieldCheck } from "lucide-react"
import Link from "next/link"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"

const verificationServices = [
  {
    title: "Voters Card",
    description: "Verify voters card information and registration status",
    icon: Vote,
    href: "/services/verification/voters-card",
    color: "bg-[#3457D5]",
  },
  {
    title: "Driver License",
    description: "Verify driver's license details and validity",
    icon: Car,
    href: "/services/verification/driver-license",
    color: "bg-[#3457D5]",
  },
  {
    title: "International Passport",
    description: "Verify international passport information",
    icon: Plane,
    href: "/services/verification/passport",
    color: "bg-[#3457D5]",
  },
  {
    title: "NIN",
    description: "Verify National Identification Number details",
    icon: ShieldCheck,
    href: "/services/verification/nin",
    color: "bg-[#3457D5]",
  },
  {
    title: "BVN",
    description: "Verify Bank Verification Number information",
    icon: CreditCard,
    href: "/services/verification/bvn",
    color: "bg-[#3457D5]",
  },
  {
    title: "Plate Number",
    description: "Verify vehicle plate number and registration",
    icon: Hash,
    href: "/services/verification/plate-number",
    color: "bg-[#3457D5]",
  },
  {
    title: "TIN",
    description: "Verify Tax Identification Number details",
    icon: FileText,
    href: "/services/verification/tin",
    color: "bg-[#3457D5]",
  },
  {
    title: "CAC",
    description: "Verify Corporate Affairs Commission registration",
    icon: Building2,
    href: "/services/verification/cac",
    color: "bg-[#3457D5]",
  },
  {
    title: "Phone Number",
    description: "Verify phone number ownership and registration",
    icon: Phone,
    href: "/services/verification/phone",
    color: "bg-[#3457D5]",
  },
]

export default function VerificationServicesPage() {
  return (
    <div className="space-y-8">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="mb-8"
      >
        <h1 className="text-4xl font-bold text-[#3457D5] mb-2">Verification Services</h1>
        <p className="text-muted-foreground">
          Verify identity documents, licenses, and registration information instantly
        </p>
      </motion.div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {verificationServices.map((service, index) => {
          const Icon = service.icon
          return (
            <motion.div
              key={service.href}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.1 }}
            >
              <Link href={service.href}>
                <Card className="h-full hover:shadow-lg transition-shadow cursor-pointer border-[#CCCCFF]/20">
                  <CardHeader>
                    <div className={`${service.color} w-12 h-12 rounded-lg flex items-center justify-center mb-4`}>
                      <Icon className="h-6 w-6 text-white" />
                    </div>
                    <CardTitle className="text-[#3457D5]">{service.title}</CardTitle>
                    <CardDescription>{service.description}</CardDescription>
                  </CardHeader>
                  <CardContent>
                    <span className="text-sm text-[#3457D5] font-medium hover:underline">Verify Now â†’</span>
                  </CardContent>
                </Card>
              </Link>
            </motion.div>
          )
        })}
      </div>
    </div>
  )
}
